                    </div>
                </div>
            </div>
            <div class="choose-stake">
                <div class="block-title">
                    投注金额
                </div>
                <div class="choose-stake-1">
                    <span class="icon icon-eos"></span>
                    <input class="hole hole-stake" v-model="eos"/>
                </div>
                <div class="choose-stake-2">
                    <button class="btn-operate" @click="setEOS(.5)">1/2</button>
                </div>
                <div class="choose-stake-3">
                    <button class="btn-operate" @click="setEOS(2)">2X</button>
                </div>
                <div class="choose-stake-4">
                    <button class="btn-operate" @click="setEOS()">MAX</button>
                </div>
            </div>
            <div class="hole gain-prize-wrap">
                <span class="gain-prize-title">获得奖金</span>
                <span class="gain-prize-num hole-num">{{payWin}}</span>
            </div>
            <div class="hole hide-more-1280">
                <div class="detail-num-wrap">
                    小于该数获胜 <br/> <span class="hole-num"> {{diceAnalytic.rollUnderNum}}</span>
                </div>
                <div class="detail-num-wrap">
                    赔率 <br/> <span class="hole-num"> {{Number(diceAnalytic.payOut).toFixed(2)}}x</span>
                </div>
                <div class="detail-num-wrap">
                    中奖概率 <br/> <span class="hole-num"> {{Number(diceAnalytic.winChance).toFixed(2)}}%</span>
                </div>
            </div>
            <div class="eos-balance-hint">
                <span class="text-hint">您的 EOS 余额：</span>
                <span>{{eosBalance + " EOS"}}</span>
            </div>
            <div class="hole hole-shake">
                <button v-if="account.name" @click="doShakeAction" class="shake-btn">摇起来</button>
                <button v-if="!account.name" @click="login" class="shake-btn">登录</button>
            </div>
        </div>
