<script>

    import network from '@/utils/network';
    import api from '@/utils/eos';
    import eventHub from '@/utils/event';
    import Eos from 'eosjs';
    import createHash from 'create-hash';
    import vueSlider from 'vue-slider-component';

    export default {
        mounted() {
            eventHub.$on('ROLLUNDER_CHANGE', rollUnder => this.rollUnder = rollUnder);
            this.getEOS();
            this.getPool();
            this.getOperateNum();
            //init dice analytics data
            this.$store.commit('UPDATE_DICE_ANYLYTICS', {
                rollUnderNum: this.rollUnder,
                payOut: 98 / (this.rollUnder -1),
                winChance: this.rollUnder -1
            })
        },
        data() {
            return {
                eosBalance: 0,
                eos: 1,
                poolBalance: 0,
                operateNum: '050',
                rollUnder: 50,
                choice: '',
                diceGameScope: 'bpeosbetdice',
                diceLogScope: 'bpeosdicelog',
                sliderOptions:{
                    min:2,
                    max: 96,
                    // minRange: 2,
                    // maxRange: 96,
                    style:{
                        // width: '95%',
                        margin: '0 auto'
                    },
                    sliderStyle:{
                        padding: 0,
                        margin: '0 auto'
                    },
                    processStyle:{
                        background: '#fff',
                    },
                    tooltip: false,
                    bgStyle: {
                        border: 'none',
                        borderRadius: '10px',
                        boxShadow: 'inset rgba(0,0,0,.1) 0 1px 1px 0, inset rgba(255,255,255,.2) 0 -1px 1px 0',
                        background: 'rgba(0, 0, 0, .05)'
                    }
                }
            };
        },
        methods: {
            getEOS() {
                if (!this.account.name) {
                    this.eosBalance = 0;
                    return;
                }
                return api.getAccount(this.account.name).then(({ core_liquid_balance }) => {
                    this.eosBalance = Number(core_liquid_balance.replace(/\sEOS/, ''));
                });
            },
            getPool() {
                Promise.all([
                api.getTableRows({
                    json: true,
                    code: 'eosio.token',
                    table: 'accounts',
                    scope: this.diceGameScope
                }),
                api.getTableRows({
                    json: true,
                    code: this.diceGameScope,
                    table: 'fundpool',
                    scope: this.diceGameScope
                })
                ]).then(([accountBalance, poolBalance]) => {
                    this.poolBalance = poolBalance.rows.length > 0 ? accountBalance.rows[0].balance.slice(0, -4)
                        - poolBalance.rows[0].locked.slice(0, -4) : accountBalance.rows[0].balance.slice(0, -4);
                });
            },
            getOperateNum() {
                let operateNum = this.rollUnder.toString();
                return this.rollUnder < 100 ? '0' + operateNum : operateNum;
            },
            floor(value, decimals) {
                return Number(Math.floor(value+'e'+decimals)+'e-'+decimals);
            },
            maxBetAmount() {
                return this.floor(this.poolBalance / 50 / (98 / this.winChance) * 0.9, 4);
            },

            getClientSeed() {
                let randomNumber = Math.floor(Math.random() * Math.floor(Number.MAX_SAFE_INTEGER));
                return createHash('sha1').update(this.account.name + Date.now() + randomNumber).digest('hex');
            },
            doShakeAction() {
                let maxAmount = this.maxBetAmount();
                if (this.eos > maxAmount) {
                  this.$notify({
                    title: 'Bet Failed',
                    text: 'Bet Amount should not be more than ' + maxAmount.toFixed(4) + ' EOS',
                    type: 'error'
                  });
                  return;
                }
                const minBetAmount = 0.1
                if (this.eos < minBetAmount) {
                this.$notify({
                    title: 'Bet Failed',
                    text: 'Bet Amount should be more than ' + minBetAmount.toFixed(4) + ' EOS',
                    type: 'error'
                });
                return;
                }

                const eos = scatter.eos(network, Eos, {})
                const options = {
                    authorization: `${this.account.name}@${this.account.authority}`,
                    broadcast: true,
                    sign: true
                };
                this.showEOSAnimation = true;
                this.$notify({
                    text: 'Waiting for Scatter to confirm transfer...'
                })
                let referrer = 'eosiosunmoon';
                let userSeed = this.getClientSeed();
                eos.transfer({
                    from: this.account.name,
                    to: this.diceGameScope,
                    quantity: Number(this.eos).toFixed(4) + ' EOS',
                    memo: `${this.rollUnder}-${userSeed}-${referrer}`
                    }).then(() => {
                    this.getEOS();
                    this.fetchResult(userSeed);
                    this.animating = true;

                    this.$notify({
                        title: 'Bet success',
                        text: 'Waiting for bet result',
                        type: 'info'
                    });
                    }).catch(e => {
                        this.$notify({
                            type: 'error',
                            text: e.message || JSON.parse(e).error.details[0].message
                        })
                    });
            },
            fetchResult(hash) {
                const operateNumInterval = setInterval(() => {
                    this.operateNum = Number(Math.random()*(96-2)+2).toFixed(0);
                });
                api.getActions(this.diceLogScope, -1, -20).then(({ actions }) => {
                const result = actions.find(action => action.action_trace
                    && action.action_trace.act
                    && action.action_trace.act.account === this.diceLogScope
                    && action.action_trace.act.name === 'result'
                    && action.action_trace.act.data
                    && action.action_trace.act.data.result
                    && action.action_trace.act.data.result.seed_hash === hash);

                if (!result) return this.fetchResult(hash);



                const { action_trace: { act: { data: { result: { amount, payout } } } } } = result;

                if (payout === '0.0000 EOS') {
                    this.showDownAnimation = true;
                    this.animationTxt = amount;
                } else {
                    this.showUpAnimation = true;
                    this.animationTxt = payout;
                }

                    clearInterval(operateNumInterval);
                    this.operateNum = amount;

                setTimeout(() => {
                    this.showDownAnimation = false;
                    this.showUpAnimation = false;
                }, 3100);

                this.animating = false;
                this.getEOS();
                });
            },
            setEOS(rate) {
                let eos = rate ? this.eos * rate : this.eosBalance;
                switch (true) {
                case (eos < 0.1):
                    eos = 0.1;
                    break;
                case (eos > this.eosBalance):
                    eos = this.eosBalance;
                    break;
                case (eos > this.maxBetAmount()):
                    eos = this.maxBetAmount();
                    break;
                }
                this.eos = Number(eos).toFixed(4);
            },
            login() {
                scatter.getIdentity({
                    accounts: [network]
                }).then(() => {
                    const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
                    if (!account) return;
                    this.$store.commit('UPDATE_ACCOUNT', account);
                }).catch(e => {
                    this.$notify({
                        type: 'warn',
                        text: e.message
                    })
                });
            },
        },
        watch: {
            account() {
                this.getEOS();
            },
            rollUnder(){
                this.$store.commit('UPDATE_DICE_ANYLYTICS', {
                    rollUnderNum: this.rollUnder,
                    payOut: 98 / (this.rollUnder -1),
                    winChance: this.rollUnder -1
                })
            }
        },

        computed: {
            operateNum1() {
                return this.operateNum.toString().slice(0, 1);
            },
            operateNum2() {
                return this.operateNum.toString().slice(1, 2);
            },
            operateNum3() {
                return this.operateNum.toString().slice(2, 3);
            },
            diceAnalytic() {
                return this.$store.state.diceAnalytic;
            },
            payWin() {
                return (this.eos * this.$store.state.diceAnalytic.payOut).toFixed(4);
            },
            account() {
                return this.$store.state.account; 
            }
        },
        components:{
            vueSlider
        }
    }
</script>


