import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        account:{

        },
        diceAnalytic:{
            rollUnderNum: '',
            payOut: '',
            winChance: ''
        }
    },
    mutations:{
        UPDATE_ACCOUNT(state, account){
            state.account = account;
        },
        UPDATE_DICE_ANYLYTICS(state, anaylytics){
            state.diceAnalytic = anaylytics
        }
    }
})