<template>
    <header>
        <!--<img class="mobile-logo" src="../assets/mobile-logo.png"/>-->
        <img class="mobile-logo-crown" src="../assets/mobile-logo-crown.png"/>
        <div class="user-operate">
            <div class="user-operate-item">
                邀请好友
            </div>
            <div class="user-operate-item">
                游戏规则
            </div>
            <div class="language-setting user-operate-item"><span class="icon icon-language"></span> 中文</div>
            <div class="log-operate user-operate-item" v-if="!account.name">
                <div @click="login">
                    <span class="icon icon-logout"></span><span>登录</span>
                </div>
            </div>
            <div class="user-operate-item user-log-out" v-if="account.name">
                <span>{{account.name}}</span>
                <div @click="logout" class="logout-btn">
                    <span class="icon icon-logout"></span><span>登出</span>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    import network from '@/utils/network';
    import eventHub from '@/utils/event';

    export default{
        methods:{
            login() {
                scatter.getIdentity({
                    accounts: [network]
                }).then(() => {
                    const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
                    if (!account) return;
                    this.$store.commit('UPDATE_ACCOUNT', account);
                }).catch(e => {
                    this.$notify({
                        type: 'warn',
                        text: e.message
                    })
                });
            },
            logout() {
                scatter.forgetIdentity().then(() => {
                    this.$notify({
                        type: 'success',
                        text: 'User logout success'
                    })
                    this.$store.commit('UPDATE_ACCOUNT', {});
                });
            }
        },
        computed:{
            account(){
                return this.$store.state.account
            }
        }
    }
</script>

