<template>
    <div class="playground-orders section-item">
        <div class="section-tab">
            <div class="section-tab-item" :class="{'active': currentTab === 1}" @click="getAllRecord">所有历史</div>
            <div class="section-tab-item" :class="{'active': currentTab === 2}" @click="getMyRecord">我的历史</div>
        </div>
        <div class="section-content">
            <table class="orders-table">
                <thead>
                <tr>
                    <th>玩家</th>
                    <th class="hide-less-1280">选择</th>
                    <th class="hide-less-1280">投注</th>
                    <th>结果</th>
                    <th>奖金</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(order,index) in orders" :key="index">
                    <td>{{order.action_trace.act.data.result.player}}</td>
                    <td class="hide-less-1280">{{order.action_trace.act.data.result.roll_under}}</td>
                    <td class="hide-less-1280">{{order.action_trace.act.data.result.amount.replace('EOS', '')}}</td>
                    <td>{{order.action_trace.act.data.result.random_roll}}</td>
                    <td class="payout-num">{{order.action_trace.act.data.result.payout !== '0.0000 EOS' && order.action_trace.act.data.result.payout.replace('EOS', '') || ''}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import api from '@/utils/eos'
    export default {
        mounted() {
            setInterval(this.fetchOrders, 1000);
        },
        data() {
            return {
                currentTab: 1,
                orders: [],
                diceLogScope: 'bpeosdicelog'
            }
        },
        methods:{
            fetchOrders() {
                api.getActions(this.diceLogScope, -1, -20).then(({ actions }) => {
                    this.orders = actions.filter(action => {
                        return action.action_trace
                            && action.action_trace.act
                            && action.action_trace.act.data
                            && action.action_trace.act.data.result
                            && action.action_trace.act.account == this.diceLogScope
                            && action.action_trace.act.name == "result";
                    }).reverse();
                });
            },
            dateFormat(raw) {
                return new Date(raw+'Z').toLocaleTimeString();
            },
            getAllRecord(){
                this.currentTab = 1;
            },
            getMyRecord(){
                this.currentTab = 2;
            }
        }
    }
</script>


<style lang="scss" scoped>
    .playground-orders {
        border-radius: 0 0 15px 15px;
    }

    .orders-table{
        width: 100%;
    }

    .orders-table th{
        opacity: .5;
    }

    .orders-table tr{
        height: 18px;
        /*padding: 10px 0;*/
    }

    .orders-table td{
        padding: 5px 0;
    }

    .payout-num{
        color: #7ed321;
    }

    @media screen and (min-width: 1280px){
        .playground-orders {
            border-radius: 0 15px 15px 0;
        }
    }
</style>